import React , {Component} from 'react'
import Image from '../img/opportunity.png'
import Footer from '../App_footer'

class Opportunity extends Component{
    render(){
        
        let style = {
            marginTop : '100px'
        }
        
        return(
            <div className='container' style = { style }>
            <div className='row'>
                <section className='col-md-4'>
                <img  src= {Image} alt='img'/>
                </section >
                <section className='col-md-8'>
                    <h1 className='highlight'>About The Opportunity</h1>
            <br/><br/>
                    <p>Our promise is to enhance people's quality of life every day through a proven business Opportunity and premium health and wellness products. Lifestyles has a rich history of helping millions of people live better every day.

People around the world, just like you, are taking advantage of our proven business Opportunity. Some are building new avenues of wealth and financial independence, while others are looking to supplement their income.

The Lifestyles Opportunity has created several self-made millionaires and has improved the health of millions worldwide. With Lifestyles, you will benefit from a business built on our industry leading products.

Not only will you enjoy improved health by using Lifestyles products, you will also experience personal growth and fulfillment as you progress through Lifestyles Distributor Career and Compensation Plan.

Lifestyles is an established and secure company that you can partner with in complete confidence. Join the Lifestyles family today and be a part of one of the most respected businesses in the industry, recognized as a world-leader in building success through health and wealth.

Your path to better health and financial freedom starts with your drive to succeed! So what are your dreams for the future? We can help you get there!</p>
                </section>
                
            
            </div>
            <Footer />
            </div>
            
        )
    }
}
export default Opportunity