import React , {Component} from 'react'
import one from './img/one.jpg'
import two from './img/two.jpg'
import three from './img/three.jpg'
import four from './img/four.jpg'
import Footer from './App_footer'

class About extends Component{
    render(){
        
        let style = {
            marginTop : '100px'
        }
        
        let carousel = {
            width : '900px',
            height : '400px'
        }
        
        
        return(
            <div className='container' style = {style}>
            <div className='row'>
                <div  className='col-md-6'>
                    <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                  <ol className="carousel-indicators">
                    <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="3"></li>
                  </ol>
                  <div className="carousel-inner" role="listbox">
                    <div className="carousel-item active">
                      <img className="d-block w-100" src={ one } data-src="holder.js/900x400?theme=social" alt="900x400" data-holder-rendered="true" style={carousel} />
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100" src={ two } data-src="holder.js/900x400?theme=industrial" alt="900x400" data-holder-rendered="true" style={carousel} />
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100" src={ three } data-src="holder.js/900x400?theme=industrial" alt="900x400" data-holder-rendered="true" style={carousel} />
                    </div>
            
                    <div className="carousel-item">
                      <img className="d-block w-100" src={ four } data-src="holder.js/900x400?theme=industrial" alt="900x400" data-holder-rendered="true" style={carousel} />
                    </div>
                  </div>
                  <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                  </a>
                  <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                  </a>
                </div>
                

            
                    
                </div>
                <div className='col-md-6'>
                    <h1 className='highlight'>Events</h1>
                    <p>Lifestyles Executive team continuously works to ensure all Lifestyles Distributors receive the guidance, support and tools they need to build a prosperous home-based business around the world. Our Executive team is always striving to create innovative health and wellness products that help people live better every day.
</p>
                </div>
            </div>
            
                <Footer />
                
            </div>
        )
    }
}
export default About
