import React , {Component} from 'react'
import Showcase from './img/showcase.png'
import Two from './img/two.png'
import Three from './img/three.png'
import Four from './img/four.png'
import Next from './img/next_one.png'
import NextTwo from './img/next_two.png'
import NextThree from './img/next_three.png'
import './styles/App_home.scss'
import Footer from './App_footer'


class Home extends Component{
    render(){
        
        let caroStyle = {
            width: '900px', 
            height: '400px'
        }
       

        
        
        return(
            <div className = 'container'>
            <div className = 'row showcase '>
                
                <section className='col-md-6 '>        
                <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                  <div className="carousel-inner" role="listbox">
                    <div className="carousel-item active">
                      <img className="d-block w-100" src={Showcase} data-src="holder.js/900x400?theme=social" alt="900x400" data-holder-rendered="true" style={caroStyle} />
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100" src= { Two } alt="900x400" data-holder-rendered="true" style={caroStyle} />
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100" src= { Three } alt="900x400" data-holder-rendered="true" style={caroStyle} />
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100" src= { Four } alt="900x400" data-holder-rendered="true" style={caroStyle} />
                    </div>
                  </div>
                  <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                  </a>
                  <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                  </a>
                </div>
                </section>
                <section className='col-md-6'>
                    <h1 className = 'highlight'>Mission</h1>
                    <p>Lifestyles mission is simple - helping people live better every day. Lifestyles is a leading global network marketing organization creating wellness and business opportunity throughout the world with our premium brands. We are passionate about helping people and positively changing lives by:</p>
                    <ul>
                        <li>Offering premium and innovative products to individuals and families worldwide</li>
                        <li>Providing business and growth opportunities for our Distributors</li>
                        <li>Recognizing the contributions of our employees</li>
                        <li>Being a responsible local and global citizen</li>
                    </ul>
                </section>
                <hr/>
                


                
                
            </div>
            
                <div className="bd-example col-md-7 mx-auto ">
                  <div id="carouselExampleCaptions" className="carousel slide demo" data-ride="carousel">
                    <ol className="carousel-indicators">
                      <li data-target="#carouselExampleCaptions" data-slide-to="0" className="active"></li>
                      <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
                      <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
                    </ol>
                    <div className="carousel-inner" role="listbox">
                      <div className="carousel-item active">
                        <img className="d-block w-100 demo" data-src="holder.js/900x300?aNameuto=yes&amp;bg=777&amp;fg=555&amp;text=First slide" alt="First slide [900x300]" src={NextTwo} data-holder-rendered="true" />
                        <div className="carousel-caption d-none d-md-block">
                         
                        </div>
                      </div>
                      <div className="carousel-item">
                        <img className="d-block w-100 demo" data-src="holder.js/900x300?auto=yes&amp;bg=666&amp;fg=444&amp;text=Second slide" alt="Second slide [900x300]" src={Next} data-holder-rendered="true" />
                        <div className="carousel-caption d-none d-md-block">
                          
                        </div>
                      </div>
            <div className="carousel-item">
                        <img className="d-block w-100 demo" data-src="holder.js/900x300?auto=yes&amp;bg=666&amp;fg=444&amp;text=Second slide" alt="Second slide [900x300]" src={NextThree} data-holder-rendered="true" />
                        <div className="carousel-caption d-none d-md-block">
                          
                        </div>
                      </div>
                    </div>
                    <a className="carousel-control-prev" href="#carouselExampleCaptions" role="button" data-slide="prev">
                      <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                      <span className="sr-only">Previous</span>
                    </a>
                    <a className="carousel-control-next" href="#carouselExampleCaptions" role="button" data-slide="next">
                      <span className="carousel-control-next-icon" aria-hidden="true"></span>
                      <span className="sr-only">Next</span>
                    </a>
                  </div>
                </div>
            
                <Footer />
            </div>
        )
    }
}
export default Home