import React, { Component } from 'react';
import Appnavbar from './components/App_navbar'
import App_home from './components/App_home'
import App_about from './components/App_about'
import App_event from './components/App_event'
import App_contact from './components/App_contact'
import App_products from './components/App_products'
import App_intra from './components/App_intra'
import {BrowserRouter , Route} from 'react-router-dom'
import App_opportunity from './components/opportunity/App_theOpportunity'
import App_businessBuildingTo from './components/opportunity/App_businessBuildingTo'
import App_gettingStarted from './components/opportunity/App_gettingStarted'
import App_compensation from './components/opportunity/App_compensation'
import App_message from './components/opportunity/App_message'


class App extends Component {
  render() {
      
      
      
    return (
        <BrowserRouter>
      <div>
        <Appnavbar />
        <Route exact path = '/intra' component = {App_home} />
        <Route path = '/products' component = {App_products} />
        <Route path = '/about' component = {App_about} />
        <Route path = '/lifestyle' component = {App_intra} />
        <Route path = '/opportunity' component = {App_opportunity} />
        <Route path = '/gettingStarted' component = {App_gettingStarted} />
        <Route path = '/business' component = {App_businessBuildingTo} />
        <Route path = '/compensation' component = {App_compensation} />
        <Route path = '/message' component = {App_message} />
        <Route path = '/contact' component = {App_contact} />
        <Route path = '/event' component = {App_event} />
        
        
      </div>
        </BrowserRouter>
        
    );
  }
}

export default App;
