import React , {Component} from 'react'
import './styles/App_navbar.scss'
import {Link} from 'react-router-dom'

class Navbar extends Component{
    render(){
        return(
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark navbar-custom">
                  
                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                  </button>

                  <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                      <li className="nav-item active">
                        <Link to = '/intra' className="nav-link" >Home <span className="sr-only">(current)</span></Link>
                      </li>
                      
        
                        <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Products
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <Link to ='/products' className="dropdown-item" >Our products</Link>
          <Link to ='./lifestyle' className="dropdown-item" >Intra</Link>
          
        </div>
      </li>
        
        <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Opportunity
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <Link to ='/opportunity' className="dropdown-item" >The Opportunity</Link>
          <Link to ='/gettingStarted' className="dropdown-item" >Getting Started</Link>
          <Link to ='/business' className="dropdown-item" >Business Building Tools</Link>
          <Link to ='/compensation' className="dropdown-item" >The Compensation Plan</Link>
          <Link to ='/message' className="dropdown-item" >A Message from Paul Kramer</Link>
          
        </div>
      </li>
                      
                        <li className="nav-item">
                        <Link to = '/about' className="nav-link" >About us</Link>
                      </li>
                        <li className="nav-item">
                        <Link to = '/contact' className="nav-link" >Contact us</Link>
                      </li>
                        <li className="nav-item">
                        <Link to = '/event' className="nav-link" >Event</Link>
                      </li>
        
                        
                    </ul>
                    
                  </div>
                </nav>

            </div>
        )
    }
}
export default Navbar
