import React , {Component} from 'react'
import Image from '../img/gettingstarted.png'
import Footer from '../App_footer'

class Opportunity extends Component{
    render(){
        
        let style = {
            marginTop : '100px'
        }
        
        return(
            <div className='container' style = { style }>
            <div className='row'>
                <section className='col-md-4'>
                    <img src = {Image} alt='img'/>
                </section >
                <section className='col-md-8'>
                    <h1 className='highlight'>Getting started</h1>
                    
                    <h5 className='highlight'>Develop Your Business At Your Own Pace</h5>
                    <br/>        
                <p>Join the Lifestyles Family and take an important step toward turning your dreams into reality.  As a Lifestyles Distributor, take comfort in the security of knowing you are part of one of the most respected companies in the industry.  As well as our support, you gain proven strategies for helping to make the most of your Lifestyles opportunity.</p>
                <h5 className='highlight'>Your membership includes:</h5>
                <ul>
                <li>World-wide Membership</li>
                <li>Distributor Kit</li>
                <li>Company Support</li>
                <li>Up-line Support</li>
                <li>Training and Education</li>
                <li>Discount/Profits and Bonuses</li>
            </ul>
            <p>Membership is only 3150¥. However, if you want to get your business off the ground right away, Lifestyles offers a number of packages to help you start earning money.

The Premier Pack
The Premier Pack gives you 1000 points worth of Lifestyles’ products at a 30% discount. With this option, you’ll have a selection of products and everything you need to grow your business at your fingertips.

Instant Direct
Lifestyles’ Instant Direct option lets you choose 2800 points worth of Lifestyles’ products at a 30% discount. You’ll also become an Direct Distributor instantly.

LIFEbuilder Program
The LIFEbuilder program uses Premier Packs to create an organization of duplication. When you sponsor 3 frontline Premier Packs, you will receive a free case of Intra that can be used personally or sold for retail profit.</p>
                
            </section>
                
            
            </div>
            <Footer />
            </div>
            
        )
    }
}
export default Opportunity