import React , {Component} from 'react'
import Footer from './App_footer'
import PictureOne from './img/intra_one.png'
import PictureTwo from './img/intra_two.png'

class Intra extends Component{
    render(){
        
        let intraStyle = {
            marginTop : '100px'
        }
        
        return(
            <div className='container' style = {intraStyle}>
            <section className='row'>
                <section className='col-md-4'>
                    <img src = {PictureOne} alt='one'/>
                </section>
                <section className='col-md-8'>
                    <h1 className='highlight'>Intra</h1>
                    <h4 className='highlight'>Lifestyles Flagship Product</h4>
                <p>For more than 3,000 years, civilizations in every corner of the world have documented the powerful effects that herbs, plants, roots, tree bark, leaves and flowers (the botanicals) have on human well-being. Intra combines this ancient knowledge with advanced science to create a nutritional supplement geared to today's lifestyles.

Intra is a pleasant tasting, proprietary formulation of 23 time-tested and trusted botanical extracts that provide the body with antioxidants, flavonoids, lignins, polysaccharides and other health enhancing nutrients specific to each herbal extract. The key to Intra's effectiveness is the synergy of the blended botanicals working together – providing greater benefits than an individual botanical on its own.

Intra's unique formula is exclusive to Lifestyles and has remained unchanged since 1992. As a natural food supplement, Intra's precise formula of 23 botanical extracts work better together to help balance and strengthen the body's eight biological systems, leaving you feeling healthier, happier and more energized! Intra is certified Safe for Athletic Use by the international Olympic Committee because it does not contain steroids or stimulants.

Intra's formula has been enjoyed by millions of satisfied customers worldwide and is suitable for all ages. An ounce of Intra a day can make a world of difference! Drink Intra. Share Intra. Every Day.</p>
                <img src = {PictureTwo} alt='two'/>
                </section>
            
            </section>
            <Footer />    
            </div>
        )
    }
}
export default Intra 