import React , {Component} from 'react'
import Image from '../img/compensation_one.png'
import ImageTwo from '../img/compensation_two.png'
import Footer from '../App_footer'

class Compensation extends Component{
    render(){
        
        let style = {
            marginTop : '100px'
        }
        
        return(
            <div className='container' style = { style }>
            <div className='row'>
                <section className='col-md-4'>
                    <img src = {Image} alt='img'/>
                </section >
                <section className='col-md-8'>
                    <h1 className='highlight'>The Compensation Plan</h1>
                    
                    <h5 className='highlight'>Develop Your Business At Your Own Pace</h5>
                    <br/>        
                <p>The Compensation Plan
The Lifestyles Compensation Plan is designed to pay you for your efforts. Whether you are building a part-time income or full-time business, the Compensation Plan is your road map to success and provides you with many rewards along the way. There are countless ways to build your Lifestyles Business at your own pace. The best part – it's all up to you!
<br/>
<br/>
Start earning more money through our 5 streams of income:</p>
            <img src = {ImageTwo} alt='img'/>
            <br/>
            <br/>
            
            <p>As part of the Lifestyles family, you will never be alone. With our comprehensive training, easy-to-understand business model, and helpful support system, you’ll learn everything you need to know to build your network and maximize your income.

The sky is the limit on your earning potential - with Lifestyles, anything is possible!</p>
                
            </section>
                
            
            </div>
            <Footer />
            </div>
            
        )
    }
}
export default Compensation