import React , {Component} from 'react'
import ProductOne from './img/product_one.png'
import ProductTwo from './img/product_two.png'
import Footer from './App_footer'


class Product extends Component{
    render(){
        
        let productStyle = {
            marginTop : '100px'
        }
        
        return(
            <div className='container' style = { productStyle }>
            <div className = 'row'>
                <section className='col-md-4'>
                    <img  src={ProductOne} alt='one'/>
                </section>
            
                <section className='col-md-8'>
                    <h1 className='highlight'>Our Products</h1>
                    <h4 className='highlight'>A Complete Approach To Better Health</h4>
                <br/>        
            <p>Lifestyles' success is driven by our Customers using our products and by our relationships with our Distributors forming the basis of many success stories for more than 30 years. This success is what drives Lifestyles to develop new and innovative products to help more and more people <b>Live Better Every Day</b>.<br/><br/>

Combining the best that science and nature have to offer, Lifestyles helps you improve and maintain your overall health and well-being through our proven line of high quality, nutritional supplements. Lifestyles is dedicated to offering breakthrough wellness products that are effective, safe and competitively priced.

All of Lifestyles products are tested by independent ISO-certified laboratories to ensure safety, purity and reliability. Lifestyles products are proudly made in Canada under the world's most stringent quality controls, regulated by Health Canada and measured by GMP and HACCP standards. With our strict control standards, Lifestyles proudly makes the following statement on each product label:Whether you are looking to optimize your health, lose weight, or conquer the signs of aging, the Lifestyles product line has something for you. With years of manufacturing excellence and millions of satisfied customers worldwide, you can be confident in knowing that you are providing the best for your body.

If you're ready to take control of your health and wellness, we will help you start today.</p>
            <img  src={ProductTwo} className = 'text-center' alt='two'/>
                
                </section>
            
            </div>
            <Footer />
            </div>
            
        )
    }
}
export default Product