import React , {Component} from 'react'
import Image from './img/contact.png'
import Footer from './App_footer'

class About extends Component{
    render(){
        
        let style = {
            marginTop : '100px'
        }
        
        let map = {
            border : '0'
        }
        
        return(
            <div className='container' style = {style}>
                <div className='row'>
                    <div className='col-md-4'>
                        <img src={Image} alt='img'/>
                    </div>
                    <div className='col-md-8'>
                        <h1 className='highlight'>Contact</h1>
                        
                        <p>Whether you are a first-time Customer or a long-time Distributor, the Lifestyles Regional Office is here to help you. Contact our customer service team regarding orders, shipping, and product related queries. For general inquiries, please contact the General Manager in your area.</p>
            
                        <p className='highlight'>Angelica Marie Claus</p>
                        <small >(+639) 922-364-6935</small><br/>
                        <small >(+639) 927-685-8675</small>
                        <br/>
                        <br/>
                        <p className='highlight'>For more information</p>
                        <small>Email: japan@lifestyles.net</small>
                        <br/>
                        <br/>
                        <br/>
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d61781.33836467335!2d120.99317459220003!3d14.58005439267055!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xa505023ae69601f!2sLifestyles+Philippines+Manila+-+Intra!5e0!3m2!1sen!2sph!4v1548583126516" width="500" height="350" title='map// eslint-disable-next-line// eslint-disable-next-line' frameborder="0" style={map}allowfullscreen></iframe>
                    </div>
                </div>
            
                <Footer />
                
            </div>
        )
    }
}
export default About
